/* ===== GLOBAL STYLES ===== */
:root {
  /* Brand Colors */
  --primary: #3b82f6;   /* Blue */
  --secondary: #8b5cf6; /* Purple */
  --light: #f9fafb;
  --dark: #111827;
  --gray: #6b7280;

  /* Typography */
  --font-primary: 'Arial', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-primary);
  background: linear-gradient(135deg, #dbeafe, #ede9fe);
  color: var(--dark);
  line-height: 1.6;
}

/* Center container */
.container {
  width: 90%;
  max-width: 400px;
  margin: auto;
  padding: 2rem;
}

/* Card style */
.card {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 8px 24px rgba(0,0,0,0.1);
  text-align: center;
  position: relative;
}

/* Dot indicators */
.dots {
  display: flex;
  justify-content: center;
  margin-bottom: 1.5rem;
}
.dot {
  width: 12px;
  height: 12px;
  margin: 0 4px;
  border-radius: 50%;
  background: #e5e7eb;
}
.dot.active {
  background: var(--primary);
}

/* Buttons */
.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to right, var(--primary), var(--secondary));
  color: white;
  padding: 0.9rem;
  border-radius: 12px;
  border: none;
  font-weight: 600;
  cursor: pointer;
  width: 100%;
  transition: opacity 0.3s;
}
.btn:hover {
  opacity: 0.9;
}

.skip {
  margin-top: 1rem;
  font-size: 0.9rem;
  color: var(--gray);
  cursor: pointer;
}

.branding {
  position: absolute;
  bottom: -60px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
}
.branding svg {
  width: 24px;
  height: 24px;
  color: var(--primary);
  margin-bottom: 0.3rem;
}
.branding p {
  font-weight: 500;
  color: var(--dark);
}
